/* âœ… Conteneur principal du tableau de bord */
.dashboard {
    display: flex;
    flex-direction: column; /* âœ… Organise les Ã©lÃ©ments en colonne */
    align-items: center; /* âœ… Centre le contenu horizontalement */
    justify-content: center; /* âœ… Centre le contenu verticalement */
    width: 100%;
    min-height: 100vh; /* âœ… Utiliser toute la hauteur de l'Ã©cran */
