/* âœ… Conteneur des graphiques */
.chart-container {
    width: 100%;
    max-width: 500px; /* âœ… Largeur maximale */
    height: 400px; /* âœ… Hauteur contrÃ´lÃ©e */
    background: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    margin: 20px auto; /* âœ… Centre chaque graphique */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

/* âœ… S'assurer que les graphiques ne dÃ©bordent pas */
.chart-container canvas {
    width: 100% !important;
    height: 100% !important;
    max-width: 350px !important; /* âœ… Limite la largeur */
    max-height: 300px !important;
}